import "survey-core/defaultV2.min.css";
import "./App.css";
import "survey-core/survey.i18n";
import { StylesManager, Model } from "survey-core";
import { Survey } from "survey-react-ui";

import { arrayToHtmlList, rankingsDiff } from "./helpers";

StylesManager.applyTheme("defaultV2");

//  
const choices = [
  "    ",
  "  ",
  "  (,    )  ",
  "       ",
  "    ",
  "   ,   ",
  "  ,    ",
  "     ",
  " ",
  "   ,     "
];

//  
const expertRanking = [
  "   ,   ",
  "  ,    ",
  "       ",
  "    ",
  "    ",
  "     ",
  "  ",
  " ",
  "  (,    )  ",
  "   ,     "
];


//  
const surveyJson = {
  surveyId: "19646bca-ff7f-408b-b358-8871bdebe394",
  surveyPostId: "10c68022-d3f2-41c8-b9ef-c65b8f0dba71",
  surveyShowDataSaving: true,
  showQuestionNumbers: "off",
  locale: "ru",
  pages: [{
    elements: [{
      type: "panel",
      name: "individualRankingPanel",
      title: " ",
      elements: [{
        type: "ranking",
        name: "individualRanking",
        title: "      :   ,   ",
        commentText: " ",
        choices: choices,
        isRequired:true,
      }]
    }]
  }, {
    elements: [
      {
        name: "groupLocalNumber",
        type: "text",
        inputType: "number",
        title: " :",
        isRequired: true,
      },
      {
        type: "panel",
        name: "groupRankingPanel",
        title: " ",
        elements: [{
          type: "ranking",
          name: "groupRanking",
          title: "      :   ,   ",
          choices: choices,
          isRequired: true
        }]
      }
    ]
  }],
};


/*       */
function generateCompletedPage (survey: Model) {
  let individualRanking = survey.getValue("individualRanking");
  let groupRanking = survey.getValue("groupRanking");
  survey.completedHtml =
   `<div class="sd-completedpanel">
      <h3> </h3>
      <p>  : <strong>${rankingsDiff(individualRanking, expertRanking)}</strong></p>
      ${arrayToHtmlList(individualRanking)}
      <hr/>
      <h3> </h3>
      <p>  : <strong>${rankingsDiff(groupRanking, expertRanking)}</strong></p>
      ${arrayToHtmlList(groupRanking)}
    </div>`;  
}


function App() {
    const survey = new Model(surveyJson);
    survey.onComplete.add((survey: Model) => generateCompletedPage(survey));

    return <Survey model={survey}></Survey>
}

export default App;
